<template>
    <div>
        <div class="loginNavFix">
            <img src="../../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">班级圈</p>
            <p class="navBtn f24 cfff pl-30" @click="turnToSend">发布</p>
        </div>
        <div class="circleBox">
            <van-tabs v-model="active" type="card" color="#7c88ee" sticky  @click="typeClick">
                <van-tab title="家长">
                    <div class="tabChild">
                        <van-tabs v-model="active1" color="#7c88ee" @click="parentClick">
                            <van-tab title="未审核">
                                <div class="item-for">
                                    <div class="item-list" v-for="(item,index) in list" :key="index" @click="turnToDetail(item.id,1,1)">
                                        <img class="item-img l" src="../../../assets/login/erha.jpg" alt="">
                                        <div class="item-right r">
                                            <div class="item-title-box">
                                                <div class="item-title l f26 c333">{{item.title}}</div>
                                            </div>
                                            <div class="item-bottom mt-30">
                                                <span class="item-time l f16 c999">{{item.create_time | fileTime(item.create_time)}}</span>
                                                <div class="operating r">
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>    
                            </van-tab>
                            <van-tab title="已审核">
                                <div class="item-for">
                                    <div class="item-list" v-for="(item,index) in list2" :key="index" @click="turnToDetail(item.id,1,2)">
                                        <img class="item-img l" src="../../../assets/login/erha.jpg" alt="">
                                        <div class="item-right r">
                                            <div class="item-title-box">
                                                <div class="item-title l f26 c333">{{item.title}}</div>
                                                <div class="item-status r f16 cfff true" v-if="item.state == 2">已通过</div>
                                                <div class="item-status r f16 cfff false" v-else-if="item.state == 3">未通过</div>
                                            </div>
                                            <div class="item-bottom mt-30">
                                                <span class="item-time l f16 c999">{{item.create_time | fileTime(item.create_time)}}</span>
                                                <div class="operating r" v-if="item.state == 2">
                                                    <div class="l">
                                                        <img class="operat-img l mt-8" src="../../../assets/index/operat.png" alt="">
                                                        <div class="operat-num f16 l ml-10">{{item.replyCount}}</div>
                                                    </div>
                                                    <div class="l ml-36">
                                                        <img class="operat-img l mt-8" src="../../../assets/index/zan.png" alt="">
                                                        <div class="operat-num f16 l ml-10">{{item.praise_num}}</div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>    
                            </van-tab>
                        </van-tabs>
                    </div>

                </van-tab>
                <van-tab title="老师">
                    <div class="tabChild">
                        <div class="item-for item-my">
                            <div class="item-list" v-for="(item,index) in list2" :key="index" @click="turnToDetail(item.id,2)">
                                <img class="item-img l" src="../../../assets/login/erha.jpg" alt="">
                                <div class="item-right r">
                                    <div class="item-title-box">
                                        <div class="item-title l f26 c333">{{item.title}}</div>
                                    </div>
                                    <div class="item-bottom mt-30">
                                        <span class="item-time l f16 c999">{{item.create_time | fileTime(item.create_time)}}</span>
                                        <div class="operating r">
                                            <div class="l">
                                                <img class="operat-img l mt-8" src="../../../assets/index/operat.png" alt="">
                                                <div class="operat-num f16 l ml-10">{{item.replyCount}}</div>
                                            </div>
                                            <div class="l ml-36">
                                                <img class="operat-img l mt-8" src="../../../assets/index/zan.png" alt="">
                                                <div class="operat-num f16 l ml-10">{{item.praise_num}}</div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>  
                    </div>
                </van-tab>
                <van-tab title="我的">
                    <div class="tabChild">
                        <div class="item-for item-my">
                            <div class="item-list" v-for="(item,index) in list4" :key="index" @click="turnToDetail(item.id,3)">
                                <img class="item-img l" src="../../../assets/login/erha.jpg" alt="">
                                <div class="item-right r">
                                    <div class="item-title-box">
                                        <div class="item-title l f26 c333">{{item.title}}</div>
                                    </div>
                                    <div class="item-bottom mt-30">
                                        <span class="item-time l f16 c999">{{item.create_time | fileTime(item.create_time)}}</span>
                                        <div class="operating r">
                                            <div class="l">
                                                <img class="operat-img l mt-8" src="../../../assets/index/operat.png" alt="">
                                                <div class="operat-num f16 l ml-10">{{item.replyCount}}</div>
                                            </div>
                                            <div class="l ml-36">
                                                <img class="operat-img l mt-8" src="../../../assets/index/zan.png" alt="">
                                                <div class="operat-num f16 l ml-10">{{item.praise_num}}</div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div> 
                    </div>
                </van-tab>
            </van-tabs>
        </div>
    </div>
</template>

<script>
import {publisFn} from '../../../lib/util'
export default {
  name: 'HelloWorld',
    data () {
        return {
            active:0,
            active1:0,
            active2:0,
            active3:0,
            list:[],//家长已审核
            list1:[],//家长未审核
            list2:[],//老师未审核
            list3:[],//老师已审核
            list4:[],//家长未审核
            list5:[],//我的已审核
        }
    },
    filters : {
        fileTime(num){
           return publisFn.dateFormat(num).substring(5,publisFn.dateFormat(num).length).replace(/\//g, "-")
        }
    },
    methods : {
       returns(){
            this.$router.push({path:'/indexs'});//返回上一层
        },
        turnToSend(){
            this.$router.push({path:'/setclasscircle'});//跳转发布
        },
        typeClick(index, title){//tab切换
            if(index == 0){
                if(this.active1 == 0){
                    this.getClassesNews(1,1);
                }else{
                    this.getClassesNews(1,4);
                }
            }else if(index == 1){
                this.getClassesNews(2,2);
            }else{
                this.getClassesNews(3,2);
            }
        },
        parentClick(index, title){//家长
            if(index == 0){
                this.getClassesNews(1,1);
            }else{
                this.getClassesNews(1,4);
            }
        },
        teacherClick(index, title){
            if(index == 0){
                this.getClassesNews(2,1);
            }else{
                this.getClassesNews(2,4);
            }
        },
        myClick(index, title){
            if(index == 0){
                this.getClassesNews(3,1);
            }else{
                this.getClassesNews(3,4);
            }
        },
        getClassesNews(type,state){//type表示家长老师，我的  state //审核中2审核通过 3审核不通过4已审核
            this.$toast.loading({
                mask: false,
                message: '加载中...',
                duration:0
            });
            var param = {
                classId : JSON.parse(localStorage.getItem('zhxy_user')).classesId,
                state:state,//审核中2审核通过 3审核不通过4已审核
            }
            if(type == 1){
                param.submitType = 1;//1家长2老师，
            }else if(type == 2){
                param.submitType = 2;
            }else{
                param.submitId = JSON.parse(localStorage.getItem('zhxy_user')).id;
            }
            this.$http({url:this.$api.class.getClassesNews},param).then(data => {
                console.log(data);
                if(data.code == 0){
                    if(type == 1 && state == 1){
                        this.list = data.data;
                    }else if(type == 1 && state == 4){
                        this.list1 = data.data;
                    }else if(type == 2){
                        this.list2 = data.data;
                    }else if(type == 3){
                        this.list4 = data.data;
                    }
                    this.$toast.clear();
                }else{
                    this.$toast(data.msg);
                }
            },err => {
                this.$toast('出错了');
            })
        },
        turnToDetail(id,type,state){//跳转详情页 type 1 家长 2 老师 ，3 我的 state 1 未审核 2已审核
            if(type == 1 && state == 1){
                this.$router.push({path:'/classcircledetail',query:{id:id,type:1,state:1}})
            }else if(type == 1 && state == 2){
                this.$router.push({path:'/classcircledetail',query:{id:id,type:1,state:2}})
            }else if(type == 2){
                this.$router.push({path:'/classcircledetail',query:{id:id,type:2}})
            }else{
                this.$router.push({path:'/classcircledetail',query:{id:id,type:3}})
            }
        }
    },
    created() {
        this.getClassesNews(1,1);
    },
}
</script>

<style scoped>
.circleBox{position:absolute;top: 0.84rem;left: 0;right: 0;bottom: 0;overflow-y: hidden;padding: 0.3rem 0 0 0 ;}
.tabChild{padding: 0.3rem}
.item-for{width: 100%;position: absolute;top: 0.88rem;left: 0;right: 0;overflow-y: auto;height: calc(100vh - 3rem)}
.item-list{clear: both;overflow:hidden;padding: 0.3rem 0;border-bottom: 1px solid #f7f7f7;}
.item-img{width: 1.6rem;height: 1.2rem;display: block;border-radius: 5px;}
.item-title-box{overflow: hidden;width: 4.94rem;}
.item-status{width: 0.7rem;height: 0.28rem;text-align: center;line-height: 0.28rem;border-radius: 3px;}
.true{background: #6add47;}
.false{background: #fa5c5c;}
.item-bottom{width: 100%;height: 0.5rem;border-radius: 2px;background: #f7f7f7;}
.item-time{padding: 0 0.3rem;line-height: 0.5rem;overflow: hidden;}
.operating{overflow: hidden;}
.operat-img{width: 0.3rem;height: 0.3rem;display: block;}
.operat-num{color: #929292;line-height: 0.5rem;}
.centerBtn{height: 0.9rem;width: 5rem;border-radius: 0.9rem; background: linear-gradient(to bottom right, #82cbff , #7b7ceb);line-height: 0.9rem; box-shadow: 0 0 10px #f0f9ff;position: fixed;bottom: 0.6rem;left: 50%;margin-left: -2.5rem; }
.item-my .item-list{padding: 0.3rem;}
</style>






